@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Tenor+Sans&display=swap');

#closing-cost-estimator-for-seller {
  font-family: "Montserrat", sans-serif !important;
  color: #000;
  font-optical-sizing: auto;
  font-size: 17px;
  font-weight: 400;
  font-style: normal;
  padding-top: 30px;
  padding-bottom: 30px;
}

#closing-cost-estimator-for-seller h2 {
  font-family: "Tenor Sans", sans-serif;
  font-size: 25px;
  font-weight: 700;
  text-transform: uppercase;
  font-style: normal;
}

#closing-cost-estimator-for-seller h4 {
  font-family: "Tenor Sans", sans-serif;
  font-weight: 700;
  color: rgba(10, 74, 124, 1);
}

#closing-cost-estimator-for-seller input[type="number"],
#closing-cost-estimator-for-seller input[type="email"],
#closing-cost-estimator-for-seller select {
  border-radius: 0;
  border: 1.5px solid rgba(10, 74, 124, 1);
}

#left label {
  font-weight: 600;
}

.buttons-block button,
.email-block button {
  width: 100%;
  margin-top: 10px;
  padding-top: 20px;
  padding-bottom: 20px;
}

#right {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

#right .col-lg-2 {
  text-align: right;
  color: #1d1919;
}

#right .title-cost {
  text-decoration: underline;
  font-weight: 500;
}

#right .numbers {
  font-weight: 500;
}

#right > .row .col-lg-8 {
  padding-bottom: 10px;
}

/* Buttons */
#reset-btn {
  font-weight: 700;
  color: rgba(10, 74, 124, 1);
  border: 2px solid rgba(10, 74, 124, 1);
  background-color: transparent;
  padding: 20px 46px;
  transition: all 0.3s ease;
  border-radius: 0;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 17px;
}
#reset-btn:hover {
  /* color: white;
  background-color: rgba(10, 74, 124, 1); */
  color: #28AEE3;
  background-color: white;
  border-color: #28AEE3;
}

#email-btn,
#send-btn {
  font-weight: 700;
  font-size: 17px;
  color: white;
  border: 2px solid rgba(10, 74, 124, 1);
  background-color: rgba(10, 74, 124, 1);
  padding: 20px 46px;
  transition: all 0.3s ease;
  border-radius: 0;
  font-weight: 700;
  text-transform: uppercase;
}
#email-btn:hover,
#send-btn:hover {
  color: rgba(10, 74, 124, 1);
  background-color: white;
}

#save-btn {
  font-weight: 700;
  color: white;
  border: 2px solid #28AEE3;
  background-color: #28AEE3;
  padding: 20px 46px;
  transition: all 0.3s ease;
  border-radius: 0;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 17px;
  margin-top: 10px;
}
#save-btn:hover {
  color: #28AEE3;
  background-color: white;
}

.form-switch {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-content: center;
  justify-content: space-between;
  align-items: stretch;
  padding-left: 0;
  padding-top: 5px;
}

.right-place label {
  padding-left: 15px;
}

.form-check-input:checked {
  background-color: #28AEE3 !important;
  border-color: #28AEE3 !important;
}

.form-check-input {
  width: 2.5em !important;
  height: 1.5em !important;
  margin-top: 0px;
}

.horizontal-line {
  border-bottom: 2px solid rgba(10, 74, 124, 1);
  margin: 10px 0;
}

.top-sel-cost {
  font-size: 18px;
  font-weight: 600;
}

.indicator1,
.indicator2 {
  display: inline-block;
  height: 0.875rem;
  width: 0.875rem;
  font-size: 0.875rem;
  border: 0.125rem solid #fff;
  margin-top: -0.125rem;
  color: rgba(10, 74, 124, 1);
  background-color: rgba(10, 74, 124, 1);
  border-radius: 10px;
  margin-right: 5px;
}
.indicator2 {
  color: #28AEE3;
  background-color: #28AEE3;
}

/* Dollar Sign */
.input-wrapper {
  position: relative;
}

.input-wrapper::before {
  content: '$';
  position: absolute;
  left: 18px;
  top: 49%;
  transform: translateY(-50%);
  color: #212529;
  font-size: 1.25rem;
}

.input-wrapper input {
  padding-left: 30px; 
}

/* Popup */
#popup {
  display: none;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: rgba(0, 0, 0, 0.8);
  color: white;
  padding: 10px 20px;
  border-radius: 5px;
}

/* MEDIA QUERY */
@media (min-width: 1024px) and (max-width: 1200px) { }
/* Laptop */
@media only screen and (max-width: 1024px) { }

/* Tablet */
@media only screen and (max-width: 768px) {
 
  #email-btn, 
  #send-btn,
  #reset-btn,
  #save-btn {
    font-size: 16px;
    padding-top: 16px;
    padding-bottom: 16px
  }
  #save-btn {
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
  }
  .text-right {
    text-align: right !important;
  }
  #right > .row .col-lg-8 {
    padding-bottom: 7px;
  }

}

/* Mobile */
@media only screen and (max-width: 525px) {
  #closing-cost-estimator-for-seller h2 {
    font-size: 22px;
  }
  #closing-cost-estimator-for-seller {
    font-size: 15px;
  }
  #closing-cost-estimator-for-seller input,
  #closing-cost-estimator-for-seller select {
    font-size: 1.1rem;
  }
  #email-btn, 
  #send-btn,
  #reset-btn,
  #save-btn {
    font-size: 15px;
    padding-top: 16px;
    padding-bottom: 16px
  }
  #save-btn {
    width: 100%;
    padding-left: 10px;
    padding-right: 10px;
  }
  .top-sel-cost {
    font-size: 15px;
  }
  .text-right {
    text-align: right !important;
  }
  #right > .row .col-lg-8 {
    padding-bottom: 2px;
  }
  #broker_comission {
    margin-bottom: 8px;
  }
  .flip-tax {
    margin-bottom: 0 !important;
  }
  #right {
    margin-top: 15px;
  }
  .input-wrapper::before {
    font-size: 1.1rem;
  }
}

@media only screen and (max-width: 375px) {
  closing-cost-estimator-for-seller h4 {
    font-size: 18px;
  }
}

@media only screen and (max-width: 325px) {}